<template>
    <div>
        <filter-drawer
        :listFilterModel="listFilterModel"
        :filterables="filterables"
        v-on:close-filter-drawer="listFilterModel=false"
        v-on:apply-filter="updateRoute"
        :clickFilter="clickFilter"></filter-drawer>
        <v-content style="">
            <v-container fluid>
                <v-layout  row mb-2>
                    <v-subheader class="headline">Products</v-subheader>
                    <v-spacer></v-spacer>
                    <v-btn class="" flat icon @click.stop="listFilterModel=!listFilterModel"><v-icon>filter_list</v-icon></v-btn>
                    <v-btn class="" flat icon @click.stop="listSettingsModel=true"><v-icon>settings</v-icon></v-btn>
                    <v-btn class="primary" to="/products/add">Add</v-btn>
                </v-layout>
                <data-table
                :route="listRoute"
                :listSettingsModel="listSettingsModel"
                v-on:close-list-settings-model="listSettingsModel=false"
                v-on:reset-item="resetItem($event)"></data-table>
            </v-container>
        </v-content>
    </div>
</template>
<script>
    import FilterDrawerComponent from '../../../js/components/FilterDrawerComponent.vue'
    import DataTableComponent from '../../../js/components/DataTableComponent.vue'
    export default{
        components:{
            'data-table':DataTableComponent,
            'filter-drawer':FilterDrawerComponent,
        },
        data(){
            return{
                listRoute:'/products?',
                baseRoute:'/products?',
                listSettingsModel:false,
                listFilterModel:false,
                filterables:[],
                clickFilter:false,
            }
        },
        mounted(){
            axios.get('/products/filterables').then((response)=>{
                if(response.status == 200)
                {
                    this.filterables = response.data.filterables
                }
                else{
                    alert('Something went wrong!!!')
                    console.log(response)
                }
            });
        },
        methods:{
            updateRoute:function(event){
                this.listRoute = this.baseRoute+event
                this.clickFilter = false
            },
            resetItem($event){
                Object.keys(this.filterables).forEach((key)=>{
                    if(key == $event){
                        if(this.filterables[key].type == 'select'){
                            this.filterables[key].value = '-1'
                        }
                        if(this.filterables[key].type == 'slider'){
                            this.filterables[key].range = this.filterables[key].default;
                        }
                    }
                });
                this.clickFilter = true;
            },
        }
    }
</script>
