<template>
    <v-content style="">
        <v-container fluid>
            <v-layout row mb-2>
                <v-subheader class="headline">Add Products</v-subheader>
            </v-layout>
            <form-builder
            :formFields="formFields"
            :addRoute="addRoute"
            :formdata="formdata"
            v-on:form-updated="updFormData">
            </form-builder>
        </v-container>
    </v-content>
</template>
<script>
import formBuilder from '../../../js/components/formBuilderComponent.vue'
export default{
    data(){
        return{
            formFields:[],
            addRoute:'',
            formdata:[],
        }
    },
    mounted(){
        axios.get('products/add').then((response)=>{
            if(response.status == 200){
                this.formFields = response.data.formfields
                this.addRoute = response.data.add_route
            }
            else{
                alert('Something went wrong!!')
                console.log(response);
            }
        })
    },
    components:{
        'form-builder': formBuilder
    },
    methods:{
        updFormData(formdata){
            Object.keys(formdata).forEach((key)=>{
                this.formdata[key] = formdata[key]
            })
        },
    }
}
</script>
